const routes = {
  PANEL: {
    PATH: "/panel",
    DESCRIPTION: "Rostering",
  },
  PUBLICATIONS: {
    PATH: "/publications",
    DESCRIPTION: "Publicações",
  },
  IMPORTS: {
    PATH: "/imports",
    DESCRIPTION: "Importações",
  },
  SCHEDULER: {
    PATH: "/scheduler",
    DESCRIPTION: "Nova Escala",
  },
  CALENDAR: {
    PATH: "/calendar",
    DESCRIPTION: "Escala",
  },
  RESTRICTIONS: {
    PATH: "/restrictions",
    DESCRIPTION: "Restrições",
  },
  RULES: {
    PATH: "/rules",
    DESCRIPTION: "Regras",
  },
  DAYOFF: {
    PATH: "/dayoff-management",
    DESCRIPTION: "Gerenciador de folgas",
  },
  DAYOFFVIEWER: {
    PATH: "/dayoff-management/viewer",
    DESCRIPTION: "Visualizador de folgas",
  },
};

const sections = {
  panel: {
    key: routes.PANEL.PATH,
    content: routes.PANEL.DESCRIPTION,
    link: true,
  },
  publications: {
    key: routes.PUBLICATIONS.PATH,
    content: routes.PUBLICATIONS.DESCRIPTION,
    link: true,
  },
  imports: {
    key: routes.IMPORTS.PATH,
    content: routes.IMPORTS.DESCRIPTION,
    link: true,
  },
  scheduler: {
    key: routes.SCHEDULER.PATH,
    content: routes.SCHEDULER.DESCRIPTION,
    link: true,
  },
  calendar: {
    key: routes.CALENDAR.PATH,
    content: routes.CALENDAR.DESCRIPTION,
    link: true,
  },
  restrictions: {
    key: routes.RESTRICTIONS.PATH,
    content: routes.RESTRICTIONS.DESCRIPTION,
    link: true,
  },
  rules: {
    key: routes.RULES.PATH,
    content: routes.RULES.DESCRIPTION,
    link: true,
  },
  dayoff: {
    key: routes.DAYOFF.PATH,
    content: routes.DAYOFF.DESCRIPTION,
    link: true,
  },
  dayoffviewer: {
    key: routes.DAYOFFVIEWER.PATH,
    content: routes.DAYOFFVIEWER.DESCRIPTION,
    link: true,
  },
};

const getBreadCrumbByRoute = async (path) => {
  switch (path) {
    case routes.PANEL.PATH:
      return [sections.panel];

    case routes.PUBLICATIONS.PATH:
      return [sections.panel, sections.publications];
    case routes.IMPORTS.PATH:
      return [sections.panel, sections.imports];
    case routes.SCHEDULER.PATH:
      return [sections.panel, sections.scheduler];
    case routes.CALENDAR.PATH:
      return [sections.panel, sections.calendar];
    case routes.RESTRICTIONS.PATH:
      return [sections.panel, sections.restrictions];
    case routes.RULES.PATH:
      return [sections.panel, sections.rules];
    case routes.DAYOFF.PATH:
      return [sections.panel, sections.dayoff];
    case routes.DAYOFFVIEWER.PATH:
      return [sections.panel, sections.dayoffviewer];
    default:
      return [sections.panel];
  }
};

const getHeaderTitleByRoute = async (path) => {
  switch (path) {
    case routes.PANEL.PATH:
      return routes.PANEL.DESCRIPTION;
    case routes.PUBLICATIONS.PATH:
      return routes.PUBLICATIONS.DESCRIPTION;
    case routes.IMPORTS.PATH:
      return routes.IMPORTS.DESCRIPTION;
    case routes.SCHEDULER.PATH:
      return routes.SCHEDULER.DESCRIPTION;
    case routes.CALENDAR.PATH:
      return routes.CALENDAR.DESCRIPTION;
    case routes.RESTRICTIONS.PATH:
      return routes.RESTRICTIONS.DESCRIPTION;
    case routes.RULES.PATH:
      return routes.RULES.DESCRIPTION;
    case routes.DAYOFF.PATH:
      return routes.DAYOFF.DESCRIPTION;
    case routes.DAYOFFVIEWER.PATH:
      return routes.DAYOFFVIEWER.DESCRIPTION;
    default:
      return routes.PANEL.DESCRIPTION;
  }
};
